<template>
	<view>
		<uni-row>
			<uni-col>
				<uni-calendar
				:insert="true"
				:lunar="true" 
				:selected="selected"
				@change="change"
				 />
			</uni-col>
		</uni-row>
		<uni-row>
			<uni-col>
				<uni-card :isShadow="true" title="上午上班卡" extra="2021-08-03" note="08:20-08:50">
					<button type="primary" disabled="true" plain="true">
						<uni-icons type="checkmarkempty" color="green"></uni-icons>已打卡
					</button>
					<text style="color: gray;">打卡时间：2021-08-03 08:23:34</text>
				</uni-card>
				<uni-card :isShadow="true" title="上午下班卡" extra="today" note="11:50-12:30">
				    <button type="primary" plain="true" @click="daka('error')">
						<uni-icons type="locked"></uni-icons>打卡
					</button>
				</uni-card>
				<uni-card :isShadow="true" title="下午上班卡" extra="today" note="13:00-13:30">
				    <button type="warn" plain="true" disabled="true">
						<uni-icons type="closeempty" color="red"></uni-icons>未打卡
					</button>
				</uni-card>
				<uni-card :isShadow="true" title="下午下班卡" extra="today" note="18:00-19:00">
				    <button type="primary" plain="true" @click="daka('success')">打卡</button>
				</uni-card>
			</uni-col>
		</uni-row>
		<uni-popup ref="popup" type="bottom">底部弹出 Popup</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				selected:[{
					date: '2021-08-01', 
					info: '签到', 
					data: { 
						custom: '自定义信息', 
						name: '自定义消息头',
					}
				}],
				today:null
			}
		},
		methods: {
			change:function(event){
				this.today = event.fulldate
				console.log(event.fulldate)
			},
			daka:function(icon){
				if(icon=='error'){
					uni.showToast({
						/* mask:true, */
						title: '未到打卡时间',
						icon:'error',//不要图标
						duration: 2000//1后消失
					});
				}
				if(icon=='success'){
					uni.request({
					    url: 'java', //仅为示例，并非真实接口地址。
					    success: (res) => {
					        console.log(res.data.code);
					    }
					});
					uni.showToast({
						/* mask:true, */
						title: '打卡成功',
						icon:'success',//不要图标
						duration: 2000//1后消失
					});
				}
				
			}
		}
	}
</script>

